.account-details{min-height:100%;display:flex;flex-direction:column}.account-details__details{display:flex;flex-direction:column;gap:.5em}.account-details__details-field{display:grid;grid-template-columns:repeat(2, auto);grid-template-rows:repeat(2, auto);padding:.5em .8em}.account-details__details-field>h3{grid-column:1/2;grid-row:1/2;align-self:end;font-size:1.3rem;text-transform:uppercase;color:var(--text-primary-darken20)}.account-details__details-field p,.account-details__details-field input{grid-column:1/2;grid-row:2/3;color:var(--text-primary);font-size:1.5rem}.account-details__details-field .flex-row{gap:.8em;grid-column:2/3;grid-row:1/3;justify-self:end}.account-details__details-field>.btn,.account-details__details-field .flex-row>.btn{grid-column:2/3;grid-row:1/3;justify-self:end;text-transform:capitalize;font-size:1.5rem;border:none;background-color:rgba(0,0,0,0)}.account-details__details-field i{position:relative;grid-column:3/4;grid-row:1/3;justify-self:end;align-self:end;padding:.5em;border-radius:50vw;text-transform:capitalize;font-size:2rem;cursor:pointer;color:var(--text-primary-darken20)}.account-details__details-field i:hover{background-color:var(--bg-dark-lighten10)}.account-details__details-field i:hover .tooltip{display:block;font-weight:normal}.account-details__details-field i.fa-xmark,.account-details__details-field i.fa-check{display:none}.account-details__details-field .modal__content{display:flex;flex-direction:column}.account-details__details-field .modal__content>p{max-width:45ch;align-self:center;text-align:center;font-size:1.5rem;color:var(--text-primary-darken50)}.account-details__details-field .modal__content>h3{align-self:center;text-align:center}.account-details__details-field .modal__content input{border-radius:var(--border-radius);background-color:var(--bg-secondary-lighten10)}.account-details__details-field .modal__content .otp-code{display:flex;align-items:center;gap:.5em}.account-details__details-field .modal__content .otp-cod